body {
  margin: 0;
  font-family: Arial, sans-serif;
  background: #f0f0f0;
}

ul {
  list-style-type: none; /* 리스트의 불릿 제거 */
  padding: 0; /* 기본 패딩 제거 */
}

.left-section li {
  margin-bottom: 2px;
}

.app-container {
  display: flex;
  flex-direction: row; /* 세로에서 가로로 변경 */
  align-items: flex-start;
}

.left-section {
  width: 200px; /* 왼쪽 섹션의 너비 설정 */
  padding: 20px;
  background: #fff;
  border-right: 2px solid #000;
  height: 100vh; /* 화면 전체 높이로 설정 */
}

.left-section ul {
  display: flex; /* 수평 배치로 변경 */
  flex-wrap: wrap; /* 내용이 넘칠 경우 다음 줄로 이동 */
  overflow-x: auto; /* 가로로 넘칠 경우 스크롤 가능 */
}

.left-section ul li{
  margin-right: 30px;
}

.left-section h3 {
  margin-top: 0;
}

.tabs {
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.tab-button {
  padding: 10px 20px;
  margin: 5px 0;
  border: none;
  background-color: #ccc;
  cursor: pointer;
}

.tab-button.active {
  background-color: #666;
  color: #fff;
}

.field {
  position: relative;
  width: 500px;
  height: 70vh;
  background: url('../public/ground.png');
  /* background: green; */
  background-size: cover; 
  background-repeat: no-repeat;
  background-position: center;
  border: 2px solid #000;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
}

.field h2 {
  position: absolute;
  top: 10px;
  left: 10px;
  margin: 0;
  color: #fff;
  z-index: 1;
}

.players {
  width: 100%;
  height: 100%;
  position: relative;
}

.player-container {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.player {
  width: 50px;
  height: 50px;
  background: red;
  border: 2px solid #000;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
}

.player-gk {
  background: yellow; /* 골키퍼를 다른 색상으로 설정 */
  color: black; /* 텍스트 색상 */
  font-weight: bold; /* 텍스트 굵게 */
}

.player-input {
  margin-top: 5px;
  padding: 3px; /* 패딩을 줄여서 높이를 줄임 */
  font-size: 15px; /* 글꼴 크기를 줄임 */
  width: 50px; /* 너비를 줄임 */
}

.reset-button {
  margin: 10px;
  padding: 10px 15px;
  background-color: #ff4d4d; /* 붉은색 배경 */
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
}

.reset-button:hover {
  background-color: #ff1a1a; /* 마우스 오버 시 색상 변경 */
}

/* ul의 높이가 커지면 옆으로 나타나게 하기 위한 스타일 */
.left-section ul {
  display: flex; /* 수평 배치로 변경 */
  flex-wrap: wrap; /* 내용이 넘칠 경우 다음 줄로 이동 */
  overflow-x: auto; /* 가로로 넘칠 경우 스크롤 가능 */
}

/* 반응형 스타일 */
@media (max-width: 390px) {
  .app-container {
    flex-direction: column; /* 세로 배치로 변경 */
  }

  .left-section {
    width: 100%; /* 너비를 100%로 설정 */
    height: 30vh; /* 높이를 30vh로 설정 */
    border-right: none; /* 오른쪽 경계선 제거 */
    border-top: 2px solid #000; /* 상단에 경계선 추가 */
    overflow-y: hidden; /* 수직 스크롤을 숨기고, 내용이 넘치면 옆으로 나열되게 설정 */
    white-space: nowrap; /* 줄 바꿈을 막고 글자가 넘칠 경우 가로로 계속 나열되게 설정 */
  }

  .left-section ul {
    display: flex; /* 수평 배치로 변경 */
    flex-wrap: wrap; /* 내용이 넘칠 경우 다음 줄로 이동 */
    overflow-x: auto; /* 가로로 넘칠 경우 스크롤 가능 */
  }
  
  .left-section ul li{
    margin-right: 30px;
  }

  .field {
    width: 100%; /* 너비를 100%로 설정 */
    height: 60vh; /* 높이를 60vh로 설정 */
  }

  .player {
    width: 40px;
    height: 40px;
    background: red;
    border: 2px solid #000;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 10px;
    font-weight: bold;
  }

  .player-gk {
    background: yellow; /* 골키퍼를 다른 색상으로 설정 */
    color: black; /* 텍스트 색상 */
    font-weight: bold; /* 텍스트 굵게 */
  }

  .player-input {
    font-size: 10px; /* 작은 화면에서는 글꼴 크기 더 줄임 */
    width: 40px; /* 작은 화면에서는 너비 더 줄임 */
  }
}
